import * as React from 'react';
import Skjema from './skjema/skjema';
import Resultat from './resultat/resultat';
import { AppState } from './ducks/reducer';
import { connect } from 'react-redux';
import BesvarelseModell from './svar/svar-modell';
import {Dispatch} from "./types";
import {reset} from "./svar/svar-duck";

interface InnholdStateProps {
    besvarteSporsmal: BesvarelseModell[];
}

interface InnholdProps {
    visResultat: boolean;
}

interface DispatchProps {
    reset: () => void;
}

type Props = InnholdStateProps & DispatchProps;

class Innhold extends React.Component<Props, InnholdProps> {
    constructor(props: Props) {
        super(props);

        this.state = { visResultat: this.props.besvarteSporsmal.length === 19 };
    }

    handleSubmit() {
        this.setState({ visResultat: true}); // Foreløpig
    }

    handleTilbake() {
        this.props.reset();
        this.setState({ visResultat: false});
    }

render() {
    const erAlleSpormalBesvart = this.state.visResultat;
    return (
            erAlleSpormalBesvart ?
                <Resultat reset={() => this.handleTilbake()}/> : <Skjema handleSubmit={() => this.handleSubmit()}/>
    );
}
}

const mapStateToProps = (state: AppState): InnholdStateProps => ({
    besvarteSporsmal: state.svar.data
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps => ({
    reset: () => dispatch(reset())
});

export default connect(mapStateToProps, mapDispatchToProps)(Innhold);



